<script>
import humcard from './HumCard.vue'
import * as Flickity from 'flickity'

export default {
  name: 'HumDetails',
  components: {
    humcard,
  },
  data() {
    return {
      ecoFullscreen: false,
      timelineFs: false,
    }
  },
  methods : {
    openTimelineFs() {
      this.timelineFs = true;
      var body = document.getElementsByTagName('body')[0];
      body.classList.add('overflow-hidden');

    },
    closeTimelineFs() {
      this.timelineFs = false;
      var body = document.getElementsByTagName('body')[0];
      body.classList.remove('overflow-hidden');
    },
    fullEcosystem() {
      this.ecoFullscreen = true;
      var body = document.getElementsByTagName('body')[0];
      body.classList.add('overflow-hidden');
    },
    toggleEmpDetails(show, detailRef, infoRef, iconRef) {
      var detailEl = this.$refs[detailRef];
      var infoEl = this.$refs[infoRef];
      var iconEl = this.$refs[iconRef];
      if(show) {
        detailEl.classList.add('opened');
        infoEl.classList.add('show-me');
        iconEl.classList.add('hide-me');
      }else {
        detailEl.classList.remove('opened');
        infoEl.classList.remove('show-me');
        iconEl.classList.remove('hide-me');
      }
    }
  },
  mounted() {
    var self = this;
    var empCarousel = this.$refs['emp-carousel'];

    var flicky = new Flickity(empCarousel, {
      dragThreshold: 10,
      accessibility: false,
      setGallerySize: false,
      imagesLoaded: true,
      contain: true,
      cellAlign: 'left',
      prevNextButtons: true,
      pageDots: false,
    });
    
  },
}
</script>

