<template>
  <div class="c-hum-org">
    <div class="hum-org-section hum-objective">
      <div class="section-data">
        <div class="section-title weight-bold">
          <span>Aims and Objectives</span>
        </div>
        <div class="section-info">
          <p>
            The HUM organisation has a set way of working.
            Digging through some of their shareholder meeting
            documents, I found this organisation matrix, which
            explains their modus operandi.
          </p>
        </div>
      </div>
      <div class="section-figure">
        <div class="c-figure-items">
          <div class="figure-item objective">
            <p>
              Revitalising damaged systems of water purification
              and food production
            </p>
          </div>
          <span class="figure-item spacer"></span>
          <div class="figure-item objective">
            <p>
              Empowering communities to better interact with water (
              from harvesting to purification)
            </p>
          </div>
          <span class="spacer full"></span>
          <div class="figure-item objective">
            <p>
              Mobilising financial and natural resources
              from other states
            </p>
          </div>
          <span class="figure-item spacer"></span>
          <div class="figure-item objective">
            <p>
              Creating knowledge systems for resilience and
              awareness around floods
            </p>
          </div>
          <div class="figure-item objective">
            <p>
              Encouraging integration of local actors and
              plants with the WARS platform
            </p>
          </div>
        </div>
      </div>
    </div>
    <div class="hum-org-section hum-structure">
      <div class="section-data">
        <div class="section-title weight-bold">
          <span>Organisation Structure</span>
        </div>
        <div class="section-info">
          <p>
            It appears that well known organisations such as
            WARS, WHEL, and MED.CN are all under control of
            HUM! These well-liked public facing organisations
            are under their direct control.
          </p>
        </div>
      </div>
      <div class="section-figure">
        <div class="c-figure-items">

          <div class="figure-item-wrapper only-desktop title-tag-wrapper" style="--n: 0;">
            <div class="figure-item structure-tag dark">
              <div class="c-tag-logo-text">hum</div>
              <div class="tag-content">
                <div class="tag-desc">
                  <p>A Humanitarian Conglomerate</p>
                </div>
              </div>
            </div>
          </div>

          <div :class="['figure-item-wrapper', tag.isBig ? 'big' : '']" :style="{'--n': index + 1}" v-for="(tag, index) in structureTags" :key="index">
            <div class="figure-item structure-tag">
              <div class="c-tag-img">
                <img :src="tag.src" class="tag-img" @click="openVrImg(tag)">
              </div>
              <div class="tag-content">
                <div class="tag-title">
                  <span>{{tag.title}}</span>
                </div>
                <div class="tag-desc">
                  <p>{{tag.desc}}</p>
                </div>
              </div>
            </div>
          </div>

        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {debounce} from 'lodash'

export default {
  name: "HumOrg",
  data() {
    return {
      structureTags: [
        {
          shortCode: 'wars',
          title: 'WARS',
          desc: 'Water Allocation and Rationing System',
          src: require('@/assets/warslogo.png'),
          vrPath: require('@/assets/hum_vr_wars.jpg'),
          isBig: true
        },
        {
          shortCode: 'fed',
          title: 'FED',
          desc: 'Farming Exponential Deployment',
          src: require('@/assets/fedlogo.png'),
          vrPath: require('@/assets/hum_vr_fed.jpg')
        },
        {
          shortCode: 'whel',
          title: 'WHEL',
          desc: 'Water Health and Environment Labour',
          src: require('@/assets/whellogo.png'),
          vrPath: require('@/assets/hum_vr_wars.jpg')
        },
        {
          shortCode: 'med',
          title: 'MED.CN',
          desc: 'Mass Environmental Disease Control',
          src: require('@/assets/medlogo.png'),
          vrPath: require('@/assets/hum_vr_wars.jpg')
        },
        {
          shortCode: 'hydro',
          title: 'Hydrosystem Cooperatives',
          desc: 'An assembly line of water relief kits for households',
          src: require('@/assets/hydrologo.png'),
          vrPath: require('@/assets/hum_vr_hydro.jpg')
        }
      ]
    }
  },
  methods: {
    openVrImg(tag) {
      var self = this;
      self.$emit('open-vr', tag.shortCode, tag.vrPath);
    }
  },
  mounted() {
  },
};
</script>

<style lang="scss" scoped>
.c-hum-org {
  background: $white;
  @include mar-t(-10vw);
  @include pad-v(10vw);
  @include pad-h(5%);
  display: flex;
  flex-direction: column;
  @include mq($desktop-breakpoint) {
    margin-top: 0;
    @include pad-v(5%);
    @include pad-h(15%);
  }
}
.hum-org-section {
    display: flex;
    flex-direction: column;
    margin: 1.6rem 0;
    @include mq($desktop-breakpoint) {
      flex-direction: row;
      margin: 3.2rem 0;
    }
    .section-data, .section-figure {
      display: flex;
      flex-direction: column;
      justify-content: center;
    }
    .section-data {
      font-size: 1.2em;
      @include mq($desktop-breakpoint) {
        flex: 0 0 50%;
      }
      .section-title {
        @include mar-b(0.6rem);
        font-family: 'Inter-semibold', sans-serif;
      }
      .section-info {
        @include mar-b(0.8rem);
      }
    }
    .section-figure {
      @include mar-v(1.6rem);
      @include mq($desktop-breakpoint) {
        flex: 1 0 50%;
        @include mar-v(0);
      }
      .c-figure-items {
        display: flex;
        flex-wrap: wrap;
        align-items: stretch;
        justify-content: center;
        position: relative;
      }
    }
  
}
.hum-org-section.hum-objective {
  @include mq($desktop-breakpoint) {
    order: 2;
  }
  .section-data {
    @include mq($desktop-breakpoint) {
      order: 2;
      text-align: right;
    }
    .section-title, .section-info {
      @include mq($desktop-breakpoint) {
        padding-left: 25%;
      }
    }
  }
  .section-figure {
    padding: 1.2rem;
    border: 1px solid $black;
    font-size: 0.8em;
    color: $humgreen;
    @include mq($desktop-breakpoint) {
      order: 1;
    }
    .c-figure-items {
      .figure-item {
        flex: 0 0 33.33%;
        margin: 0.8rem 0 2rem;
        &.objective {
          p {
            position: relative;
            &::after {
              content: '';
              display: block;
              position: absolute;
              width: 0.8rem;
              height: 0.8rem;
              border-radius: 50%;
              background: $humgreen;
              z-index: 1;
            }
          }
          &:nth-of-type(1) p {
            text-align: right;
            &::after {
              right: 0;
              bottom: -1rem;
            }
          }
          &:nth-of-type(2) p {
            text-align: left;
            &::after {
              left: 0;
              bottom: -1rem;
            }
          }
          &:nth-of-type(3) p {
            text-align: right;
            &::after {
              right: 0;
              top: -1rem;
            }
          }
          &:nth-of-type(4) p {
            text-align: left;
            margin-left: -0.8rem;
            &::after {
              left: 0;
              top: -1rem;
            }
          }
          &:nth-of-type(5) p {
            text-align: left;
            margin: 0 -0.4rem;
            &::after {
              left: 50%;
              top: -1.2rem;
              transform: translateX(-50%);
            }
          }
        }
      }
    }
  }
}
.hum-org-section.hum-structure {
  @include mq($desktop-breakpoint) {
    order: 1;
  }
  .section-data {
    .section-title, .section-info {
      @include mq($desktop-breakpoint) {
        padding-right: 25%;
      }
    }
  }
  .section-figure {
    .c-figure-items {
      justify-content: space-between;
      @include mq($desktop-breakpoint) {
        justify-content: flex-start;
      }
      .figure-item-wrapper {
        flex: 0 0 calc(50% - 0.6rem);
        display: flex;
        flex-direction: column;
        padding: 0.6rem 0;
        @include mq($desktop-breakpoint) {
          flex: 0 0 65%;
          margin-left: calc(var(--n, 0) * 7%);
          position: relative;
          &:not(.title-tag-wrapper):before {
            content: '';
            position: absolute;
            width: 100%;
            height: 100%;
            --factor: calc(var(--n, 0) * 7);
            left: calc((var(--factor) / 65) * -100% + 1.2rem);
            bottom: 50%;
            border: 1px solid $black;
            border-width: 0 0 1px 1px;
            z-index: 0;
          }
        }
        &.title-tag-wrapper {
          display: none;
          @include mq($desktop-breakpoint) {
            display: flex;
          }
        }
        &.big {
          flex: 0 0 100%;
          @include mq($desktop-breakpoint) {
            flex: 0 0 65%;
          }
          .figure-item.structure-tag {
            flex-direction: row;
            border-radius: 200vw;
            @include mq($desktop-breakpoint) {
              border-radius: 1.2rem;
            }
            .c-tag-img {
              flex: 1 0 0;
            }
            .tag-content {
              flex: 4 0 0;
              text-align: left;
              @include mq($desktop-breakpoint) {
                flex: 3 0 0;
              }
            }
          }
        }
      }
    }
  }
}

.hum-org-section.hum-structure {
  .figure-item.structure-tag {
    flex: 1 0 auto;
    padding: 0.6rem 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border: 1px solid $black;
    border-radius: 1.2rem;
    background: $white;
    z-index: 1;
    @include mq($desktop-breakpoint) {
      flex-direction: row;
    }
    &.dark {
      background: $black;
      color: $white;
    }
    .c-tag-logo-text {
      font-size: 2rem;
      font-family: 'Bobo', sans-serif;
      padding: 0.8rem 0;
      margin: 0 0.8rem;
    }
    .c-tag-img {
      display: flex;
      flex-direction: column;
      max-width: 50%;
      cursor: pointer;
      @include mq($desktop-breakpoint) {
        flex: 1 0 0;
        margin-left: 0.8rem;
      }
      .tag-img {
        width: 100%;
      }
    }
    .tag-content {
      display: flex;
      flex-direction: column;
      font-size: 1em;
      text-align: center;
      @include mq($desktop-breakpoint) {
        flex: 3 0 0;
        font-size: 0.8em;
        text-align: left;
        margin: 0 0.8rem;
      }
      .tag-title {
        font-family: 'Inter-semibold', sans-serif;
        @include mar-b(0.4rem);
      }
    }
  }
}
  
</style>