<template>
  <div id="hum-home">
    <!-- vr viewer popup - absolute(fixed) positioned -->
    <div class="home-vr-viewer" :class="{show: showVrPopup}">
      <div class="vr-content">
        <div class="content-pano-img" ref="content-pano"></div>
        <!-- loading indicator -->
        <div v-if="contentPanoLoading" class="content-loading">
          <div class="spinner">
            <div class="dot1"></div>
            <div class="dot2"></div>
          </div>
          <div class="loading-text">
            <p>Loading Scene...</p>
          </div>
        </div>
        <!-- vr-close container -->
        <div class="c-vr-close" @click="closeVr">
          <svg-icon name="closeCircle"></svg-icon>
        </div>
        <!-- overlay text -->
        <div class="vr-textual-info weight-light">
          <p>{{contentPanoText}}</p>
        </div>

      </div>
    </div>
    <HumHeader :options="headerOptions" v-on:header-mounted="processHeaderPano" clip/>
    <HomeAbout />
    <HumEvidence />
    <HumOrg v-on:open-vr="processContentPano"/>
    <HumExtras />
    <HumDetails />
    <HumFooter />
  </div>
</template>

<script>
import HumHeader from '@/components/HumHeader.vue'
import HomeAbout from '@/components/HomeAbout.vue'
import HumEvidence from '@/components/HumEvidence.vue'
import HumOrg from '@/components/HumOrg.vue'
import HumExtras from '@/components/HumExtras.vue'
import HumDetails from '@/components/HumDetails.vue'
import HumFooter from '@/components/HumFooter.vue'

import {PanoViewer} from '@egjs/view360'

export default {
  name: 'Home',
  components: {
    HumHeader,
    HomeAbout,
    HumEvidence,
    HumOrg,
    HumExtras,
    HumDetails,
    HumFooter,
  },
  data() {
    return {
      vrTextArray: {
        wars: `Through sheer luck I met an ex-HUM operative at a farm-market, through whom I obtained additional confidential material, the most interesting of which is the HUM Ecosystem Map. This diagram meticulously lays out the structure of HUM, and how their public facing organisations worked together post Storm Lata.`,
        med: `I captured this scene during my field visit to one of the bastis in South Goa recovering from the aftermath of the storm. As you can see infront, the water supply here has been severely affected and the WHEL water truck is distributing clean drinking water. Behind me, the frontline health worker is on one of her community health checkup rounds, collecting data for the MEDCN health servers.`,
        fed: `I recently got access into a FED Farm! Had to pull some strings, the few that I do have. I had a brief conversation with the farmer you see ahead; he migrated from a neighbouring state during the droughts there to the water-rich Goa (he couldn't have predicted the storm). He's now working on testing new technologies that can help produce low-resource food for neighbouring communities, making families more resilient to future disasters.`
      },
      vrTextAlias: {
        wars: 'wars',
        med: 'med',
        whel: 'med',
        fed: 'fed',
        hydro: 'fed'
      },
      vrTextShortCode: null,
      headerOptions: {
        overlay: false,
        overlayType: 'dark'
      },
      headerPanoviewer: null,
      contentPanoviewer: null,
      contentPanoLoading: false,
      headerPanoRafId: null,
      contentPanoRafId: null,
      showVrPopup: false,
      desktopMode: false
    }
  },
  computed: {
    contentPanoText() {
      if(this.vrTextShortCode) {

        var textInfo = this.vrTextArray[this.vrTextShortCode] || this.vrTextArray[this.vrTextAlias[this.vrTextShortCode]];
        return textInfo;
      }
      return '';
    }
  },
  methods: {
    disableBodyScroll() {
      var body = document.getElementsByTagName('body')[0];
      body.classList.add('overflow-hidden');
    },
    enableBodyScroll() {
      var body = document.getElementsByTagName('body')[0];
      body.classList.remove('overflow-hidden');
    },
    closeVr() {
      this.enableBodyScroll();
      window.cancelAnimationFrame(this.contentPanoRafId);
      this.contentPanoviewer.setImage(null);
      // this.contentPanoviewer.destroy();
      this.contentPanoLoading = false;
      this.showVrPopup = false;
    },
    processHeaderPano(bannerElem, desktopMode = false) {
      var that = this;
      that.desktopMode = desktopMode;
      var height = bannerElem.clientHeight || 480;
      var width = (height * 2) + 'px';
      bannerElem.style.width = width;
      var gyro = 'yawPitch';
      if(that.desktopMode) {
        gyro = 'none';
      }
      if(!that.headerPanoviewer) {
        that.headerPanoviewer = new PanoViewer(bannerElem, {
          image: require('@/assets/hum_vr_pod.jpg'),
          gyroMode: gyro,
          touchDirection: PanoViewer.TOUCH_DIRECTION.NONE,
        });
        that.headerPanoviewer.on('ready', function(event) {
          that.applyAutoRotate(event.currentTarget, 'headerPanoRafId');
        });

      }else {
        that.headerPanoviewer.updateViewportDimensions();
      }

    },
    processContentPano(shortCode, vrImgPath) {
      var that = this;
      that.disableBodyScroll();
      that.vrTextShortCode = shortCode;
      that.showVrPopup = true;
      that.contentPanoLoading = true;
      that.desktopMode = that.getDesktopMode();
      var homeVrElem = that.$refs['content-pano'];
      var gyro = 'yawPitch';
      if(that.desktopMode) {
        gyro = 'none';
      }
      if(!that.contentPanoviewer) {
        that.contentPanoviewer = new PanoViewer(homeVrElem, {
          image: vrImgPath,
          gyroMode: gyro,
          useZoom: false,
        });
        that.contentPanoviewer.on('ready', function(event) {
          event.currentTarget.updateViewportDimensions();
          that.contentPanoLoading = false;
          that.applyAutoRotate(event.currentTarget, 'contentPanoRafId');
        });
      }else {
        that.contentPanoviewer.setImage(vrImgPath);
        that.contentPanoviewer.setGyroMode(gyro);
        // that.applyAutoRotate(desktopMode, that.contentPanoviewer, 'contentPanoRafId');
      }
      
      that.$nextTick(function() {
        setTimeout(function() {
          that.contentPanoviewer.updateViewportDimensions();
        }, 0);
      });

    },
    getDesktopMode() {
      var vw = Math.max(document.documentElement.clientWidth || 0, window.innerWidth || 0);
      if(vw < 1024) {
        return false;
      }else {
        return true;
      }
    },
    applyAutoRotate(panoviewer, rafId) {
      var that = this;
      if(!that.desktopMode) {
        return 0;
      }else {
        that.autoRotate(panoviewer, rafId);
      }
    },
    autoRotate(panoviewer, rafId) {
      var that = this;
      var start = new Date().getTime();
      var pitch = panoviewer.getPitch();
      var yaw = panoviewer.getYaw();
      var delta = 0;

      function lookAround() {
        delta = (new Date().getTime() - start);
        panoviewer.lookAt({
            yaw: yaw + (delta / 100) % 360,
            pitch: pitch
        }, 0);

        that[rafId] = window.requestAnimationFrame(lookAround);
      }

      that[rafId] = window.requestAnimationFrame(lookAround);
    }
  },
  beforeRouteLeave (to, from, next) {
    window.cancelAnimationFrame(this.headerPanoRafId);
    window.cancelAnimationFrame(this.contentPanoRafId);
    next();
  },
}
</script>

