    font-size: 1em;
    // font-family: 'Inter', sans-serif;
    @include mq($desktop-breakpoint) {
      padding: 1.2rem 20%;
    }
  }
  .c-vr-close {
    cursor: pointer;
    position: absolute;
    top: 0;
