<template>
  <svg
    class="svg-icon"
    viewBox="0 0 24 24"
  >
    <path :d="path" />
  </svg>
</template>

<script>
import icons from '@/js/svg-icons';

export default {
  name: 'svg-icon',
  props: {
    name: String,
  },
  computed: {
    path() {
      return icons[this.name];
    },
  },
};
</script>