<template>
  <div class="c-hum-card">
    <div :class="['card-info', infoBg]">
      <div class="card-title">
        <span class="title">{{title}}</span>
      </div>
      <div class="card-desc">
        <slot name="desc"></slot>
      </div>
      <!-- card actions - absolute (changes parent on media queries) -->
      <div class="card-actions">
        <div class="c-action-items">
          <slot name="actions"></slot>
        </div>
      </div>
    </div>
    <div class="card-content" :class="{fullscreen: isFullscreen}">
      <div v-if="loneImgName" class="c-img-content">
        <img :src="require(`@/assets/${loneImgName}`)" />
        <!-- close button for fullscreen ecosystem - absolute positioned -->
        <div v-if="loneFullscreen" class="fs-close" @click="closeFs">
          <svg-icon name="closeCircle" class="close-icon"></svg-icon>
        </div>
      </div>
      <slot v-if="!loneImgName" name="content"></slot>
    </div>
  </div>
</template>

<script>

export default {
  name: 'humcard',
  props: {
    title: String,
    infoBg: String,
    loneImgName: String,
    loneFullscreen: {
      type: Boolean,
      default: false
    },
  },
  model: {
    prop: 'loneFullscreen',
    event: 'fschange'
  },
  computed: {
    isFullscreen: {
      get: function() {
        return this.loneFullscreen;
      },
      set: function(value) {
        this.$emit('fschange', value);
      }
    }
  },
  methods: {
    closeFs() {
      this.isFullscreen = false;
      var body = document.getElementsByTagName('body')[0];
      body.classList.remove('overflow-hidden');
    },
  },
}
</script>

<style scoped lang="scss">
.c-hum-card {
  position: relative;
  display: flex;
  flex-direction: column;
  // flex-wrap: wrap;
  align-items: stretch;
  justify-content: center;
  border: 1px solid $black;
  @include mq($desktop-breakpoint) {
    flex-direction: row;
  }
}

.card-info {
  padding: 1.6rem;
  padding-right: 25%;
  background: $black;
  color: $white;
  @include mq($desktop-breakpoint) {
    flex: 5 0 0;
    padding-right: 1.6rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  &.humgreen {
    background: $humgreen;
  }
  .card-title {
    font-family: 'Bobo', sans-serif;
    font-size: 1.5em;
    @include mar-b(8px);
    @include mar-t(16px);
    @include mq($desktop-breakpoint) {
      @include pad-h(25%);
    }
    @include mq($md-breakpoint) {
      font-size: 2.4rem;
    }
  }
  .card-desc {
    @include mar-b(8px);
    font-size: 1.1em;
    @include mq($desktop-breakpoint) {
      @include pad-h(25%);
    }
    @include mq($md-breakpoint) {
      font-size: 1.4em;
    }
  }
}

.card-content {
  @include pad(16px);
  @include mar-b(calc(1.2rem + 16px));
  display: flex;
  flex-direction: column;
  overflow: hidden;
  @include mq($desktop-breakpoint) {
    flex: 7 0 0;
  }
  .fs-close {
    position: fixed;
    top: 0;
    right: 0;
    font-size: 3.2rem;
    width: 3.2rem;
    height: 3.2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 100;
    background: $white;
    border-radius: 50%;
    color: $black;
  }
  &.fullscreen {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    margin: 0;
    background: $white;
    z-index: 99;
    .c-img-content {
      border: 1px solid $black;
      height: auto;
      flex: 1 0 0;
      margin: 0;
      max-height: none;
    }
  }
  .c-img-content {
    overflow: hidden;
    overflow-x: scroll;
    min-height: 320px;
    height: 60vw;
    max-height: 420px;
    @include mar-h(-16px);
    @include mq($desktop-breakpoint) {
      min-height: 420px;
      height: 40vw;
      max-height: 640px;
    }
    img {
      height: 100%;
    }
  }
}

// card actions
.card-actions {
  position: absolute;
  top: 100%;
  right: 0;
  @include pad-h(16px);
  @include pad-b(16px);
  transform: translateY(-100%);
  font-size: 1.2rem;
  box-sizing: content-box;
  display: flex;
  color: $black;
  .c-action-items {
    display: flex;
  }
}


</style>
