<template>
<div class="c-hum-extras">
  <div class="extra-content">
    <div class="c-extra-img">
      <div class="c-pano-img"></div>
    </div>
    <div class="c-extra-info">
      <div class="info-title weight-bold">
        <span>A Discreet Meeting</span>
      </div>
      <div class="info-desc">
        <p>Through sheer luck I met an ex-HUM operative at a farm-market, through whom I obtained additional confidential material, the most interesting of which is the HUM Ecosystem Map. This diagram meticulously lays out the structure of HUM, and how their public facing organisations worked together post Storm Lata.</p>
      </div>
    </div>
  </div>
</div>
</template>

<script>
import {PanoViewer} from '@egjs/view360'

export default {
  name: 'HumExtras',
  data() {
    return {
      panoviewer: null
    }
  },
  mounted() {
    var self = this;
    var panoElem = document.querySelector('.c-pano-img');
    self.panoviewer = new PanoViewer(panoElem, {
      image: require('@/assets/hum_vr_wars.jpg'),
      gyroMode: 'yawPitch',
      useZoom: false,
      
    });
    self.panoviewer.on('ready', function(event) {
      event.currentTarget.updateViewportDimensions();
    });

    window.addEventListener('resize', function() {
      var vw = Math.max(document.documentElement.clientWidth || 0, window.innerWidth || 0);
      self.panoviewer.updateViewportDimensions();
    });

  }
};
</script>

<style lang="scss" scoped>
.c-hum-extras {
  display: flex;
  flex-direction: column;
  .extra-content {
    display: flex;
    flex-direction: column;
    position: relative;
    .c-extra-img {
      height: 75vw;
      max-height: 420px;
      overflow: hidden;
      display: flex;
      flex-direction: column;
      @include mq($desktop-breakpoint) {
        height: 60vw;
        max-height: 1198px;
      }
      .c-pano-img {
        height: 100%;
        position: relative;
      }
    }
    .c-extra-info {
      background: $black;
      color: $white;
      position: relative;
      top: -2.4rem;
      display: flex;
      flex-direction: column;
      @include pad-v(1.6rem);
      @include pad-h(5%);
      @include mq($desktop-breakpoint) {
        position: absolute;
