<template>
  <div class="c-hum-header" :class="{'clip-it': clip}">
    <div class="header-content">
      <div class="c-header-bg overflow-hidden">
        <slot name="header-bg">
          <!-- fallback content - header banner for home view -->
          <div class="home-banner" ref="banner"></div>
        </slot>
      </div>
      <div class="header-body overflow-hidden">
        <div class="header-body-content" :class="getClass">
          <div class="content-main">
            <slot name="header-body">
              <!-- fallback content - header body text for home view -->
              <div class="home-header-body-text">
                <p>
                  Hi, my name is Luna.<br/><br/>I have been investigating the HUM Organisation for the last couple of months.
                </p>
                <p>
                  Below, you will find my research notes on my progress so far. It is incredibly important that this information be made public inspite of forces that don’t wish for this to happen.
                </p>
                <p>Look. Share.</p>
              </div>
            </slot>
          </div>
        </div>
        <!-- down arrow - absolute positioned -->
        <svg-icon name="arrowDown" class="go-down"></svg-icon>
      </div>
    </div>
  </div>
</template>

<script>
import svgIcon from './SvgIcon.vue'

export default {
  name: "HumHeader",
  components: {
    svgIcon
  },
  props: {
    options: {
      type: Object,
      default() {
        return {
          overlay: false
        }
      }
    },
    clip: {
      type: Boolean,
      default: false
    },
  },
  computed: {
    getClass: function() {
      var classNames = "";
      if (this.options.overlay) {
        var type = this.options.overlayType || "";
        classNames += "overlay " + type;
      }
      return classNames;
    },
  },
  mounted() {
    var self = this;
    var banner = this.$refs.banner;
    if (banner) {
      var vw = Math.max(document.documentElement.clientWidth || 0, window.innerWidth || 0);
      if(vw < 1024) {
        self.$emit('header-mounted', banner);
      }else {
        self.$emit('header-mounted', banner, true);
      }
    }
    window.addEventListener('resize', function() {
      var vw = Math.max(document.documentElement.clientWidth || 0, window.innerWidth || 0);
      if(vw < 1024) {
        self.$emit('header-mounted', banner);
      }else {
        self.$emit('header-mounted', banner, true);
      }
    });
  }
};
</script>

<style lang="scss" scoped>
// header bg (banner) styles for home view
  .home-banner {
  position: absolute;
  left: 100%;
  height: 100%;
  transform: translateX(-50%);
  // width: 800px;
  @include mq(480px) {
    left: 50%;
  }
}



// header body styles for home view
.home-header-body-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  // box-sizing: content-box;
  font-size: 1.6em;
  font-family: 'Inter-semibold', sans-serif;
  color: $white;
  @include mq($desktop-breakpoint) {
    width: 50%;
    max-width: 50%;
  }
  p {
    @include mar-v(16px);
  }
}

// component styles

.c-hum-header {
  position: relative;
  display: flex;
  flex-direction: column;
  color: $white;
  z-index: 9;
  &.clip-it {
    clip-path: polygon(0 0, 100% 0, 100% calc(100% - 10vw), 0 100%);
    .go-down {
      bottom: calc(3rem + 10vw);
    }
  }
  .header-content {
    display: flex;
    flex-direction: column;
    // min-height: 640px;
    // height: 640px;
    height: 100vh;
  }
  @include mq($desktop-breakpoint) {
    &.clip-it {
      clip-path: none;
      .go-down {
        bottom: calc(3rem + 5vw);
      }
    }
    .header-content {
      // height: 1080px;
    }
  }
}



.c-header-bg {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
}

.header-body {
  flex: 1 0 auto;
  display: flex;
  flex-direction: column;
  position: relative;
  .header-body-content{
    flex: 1 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    .content-main {
      position: relative;
      flex: 1 0 auto;
      display: flex;
      flex-direction: column;
      justify-content: center;
      box-sizing: content-box;
      @include pad(5%);
    }
  }
}

.go-down {
  width: 2.4em;
  height: 2.4em;
  position: absolute;
  bottom: 3rem;
  left: 50%;
  transform: translateX(-50%);
  animation: upDownAnim 700ms linear infinite;
}




</style>
