<template>
  <!-- navbar - absolute positioned -->
  <div class="header-nav" :class="{'dark-text': darkText}">
    <div class="nav-logo-text">hum</div>
    <div class="spacer"></div>
    <div :class="['c-nav-items', showNav ? 'show' : 'hide']">
      <ul class="items-list">
        <li v-if="nextRoute != 'Home'" class="nav-item" style="--n: 0">
          <router-link v-on:click.native="toggleNav" class="nav-link" to="/"><span>HUM.2035</span></router-link>
        </li>
        <li v-if="nextRoute != 'About'" class="nav-item" style="--n: 1">
          <router-link v-on:click.native="toggleNav" class="nav-link" to="/about"><span >About</span></router-link>
        </li>
        <li v-if="nextRoute != 'Toolkit'" class="nav-item" style="--n: 2">
          <router-link v-on:click.native="toggleNav" class="nav-link" to="/toolkit"><span>Toolkit</span></router-link>
        </li>
      </ul>
    </div>
    <div class="toggle-icon" v-on:click="toggleNav">
      <svg-icon :name="iconName"></svg-icon>
    </div>
  </div>
</template>

<script>
import svgIcon from './SvgIcon.vue'
import router from '../router'

export default {
  name: 'HumNav',
  components: {
    svgIcon,
  },
  data: function() {
    return {
      iconName: 'dotCircle',
      showNav: false,
      darkText: false,
      nextRoute: 'Home'
    }
  },
  methods: {
    toggleNav: function() {
      this.showNav = !this.showNav;
      if(this.showNav) {
        this.iconName = 'closeCircle';
      }else {
        this.iconName = 'dotCircle';
      }
    },
  },
  created() {
    var self = this;
    router.afterEach(function(to, from) {
      self.nextRoute = to.name;
      if(to.name == 'About') {
        self.darkText = true;
      }else {
        self.darkText = false;
      }
      // next();
    });
  },

}
</script>

<style lang="scss" scoped>
.header-nav {
  z-index: 99;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  @include pad(5%);
  box-sizing: content-box;
  height: 3.6em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: $white;
  &.dark-text {
    color: $black;
  }
  .nav-logo-text {
    @include mar-r(1em);
    display: flex;
    align-items: center;
    font-size: 1.8em;
    font-family: 'Bobo', sans-serif;
    @include mq($md-breakpoint) {
      font-size: 2.8em;
    }
  }
  .c-nav-items {
    position: relative;
    display: flex;
    font-size: 1.2em;
    transition: top 0.25s ease-in-out;
    @include mq($md-breakpoint) {
      font-size: 1.5em;
    }
    &.show {
      top: 0;
      .items-list {
        .nav-item {
          transform: translateY(0);
        }
      }
    }
    &.hide {
      top: calc(-100% - 1em);
      @include mq($desktop-breakpoint) {
        top: 0;
      }
      .items-list {
        .nav-item {
          transform: translateY(-1em);
          @include mq($desktop-breakpoint) {
            transform: translateY(0);
          }
        }
      }
    }
    .items-list {
      list-style-type: none;
      @include mar(0);
      @include pad(0);
      display: flex;
      .nav-item {
        @include mar-h(0.5em);
        cursor: pointer;
        transition: transform 0.2s ease-in;
        transition-delay: calc(var(--n, 1) * 0.1s);
        .nav-link:hover {
          border-bottom: 1px solid $white;
        }
      }
    }
  }
  .toggle-icon {
    display: flex;
    font-size: 2.4em;
    @include mar-l(1rem);
    @include mq($desktop-breakpoint) {
      display: none;
    }
  }
}
</style>